$primaryColor: #ff5e62;
$secondaryColor: #ff94a1;

.love-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: $secondaryColor;

  .heart {
    color: $primaryColor;
    font-size: 4em;
  }

  input {
    margin: 10px;
    padding: 8px;
    font-size: 1em;
    border: 2px solid $primaryColor;
    border-radius: 5px;
  }

  button {
    margin-top: 10px;
    padding: 10px 20px;
    font-size: 1.2em;
    background-color: $primaryColor;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;

    &:hover {
      background-color: darken($primaryColor, 10%);
    }
  }

  .love-result {
    margin-top: 20px;
    font-size: 1.5em;

    p {
      margin: 5px 0;
    }
  }
}
